<template>
    <p-card style="width: 800px; margin: auto">
        <template #content>
            <div class="p-d-flex p-jc-between p-mb-3">
                <strong style="font-size: 1.5rem">Media Inventory</strong>
            </div>

            <p-data-table
                :value="mediaInventoryList"
                responsiveLayout="scroll"
                showGridlines
                class="p-datatable-sm p-mb-1"
            >
                <p-column header="Type" field="type" />
                <p-column header="Lot Number" field="lotNumber" />
                <p-column header="Start Date" field="startDate"/>
                <p-column header="End Date" field="endDate"/>
                <p-column style="width: 50px">
                    <template #body="{data}">
                        <p-button
                            icon="pi pi-pencil"
                            @click="showEditMediaInventoryDialog(data)"
                        />
                    </template>
                </p-column>
            </p-data-table>

            <div class="p-d-flex p-jc-between p-mt-2">
                <p-button
                    label="Add"
                    autofocus
                    @click="showAddMediaInventoryDialog"
                />
            </div>
        </template>
    </p-card>

    <p-dialog
        v-model:visible="addMediaInventoryDialog"
        :modal="true"
        style="width: 600px"
    >
        <template #header>
            <div class="table-header">
                <strong>{{ formIsEdit ? 'Edit Media' : 'Add Media' }}</strong>
            </div>
        </template>

        <div class="p-fluid">
            <div class="p-field">
                <label for="type">Type</label>
                <p-input-text v-model="form.type" id="type" />
            </div>
            <div class="p-field">
                <label for="lotNumber">Lot Number</label>
                <p-input-text v-model="form.lotNumber" id="lotNumber" />
            </div>
            <div class="p-field">
                <label for="startDate">Start Date</label>
                <p-calendar dateFormat="mm/dd/y" v-model="form.startDate" id="startDate" :showIcon="true" />
            </div>
            <div class="p-field">
                <label for="endDate">End Date</label>
                <p-calendar dateFormat="mm/dd/y" v-model="form.endDate" id="endDate" :showIcon="true" />
            </div>
        </div>

        <div class="p-d-flex p-mt-6">
            <p-button
                label="Update"
                class="p-mr-3"
                autofocus
                @click="save"
                v-if="formIsEdit"
            />
            <p-button label="Save" autofocus @click="save" v-else />
            <p-button
                label="Delete"
                class="p-button-danger"
                @click="confirmDelete"
                v-if="formIsEdit"
            />
        </div>
    </p-dialog>
    
    <!-- Delete Confirmation Dialog -->
    <p-dialog
        v-model:visible="deleteMediaInventoryDialog"
        modal
        header="Delete Media Inventory!!"
        :style="{ width: '30rem' }"
    >
        <p>
            Are you sure you want to delete?
        </p>
        <p-button
            label="Yes"
            class="p-button-danger p-mr-2"
            @click="deleteMedia"
        />
        <p-button label="No" class="p-mr-2" @click="cancelDeleteMediaInventory" />
    </p-dialog>
</template>

<script>
import { reactive, ref, onMounted } from "vue";
import { $axios } from "@/service/app-http";

export default {
    setup() {
        const addMediaInventoryDialog = ref(false);
        const deleteMediaInventoryDialog = ref(false);
        const formIsEdit = ref(false);

        const formatDate = (date) => {
            const d = new Date(date);
            let month = "" + (d.getMonth() + 1);
            let day = "" + d.getDate();
            const year = d.getFullYear();

            if (month.length < 2) month = "0" + month;
            if (day.length < 2) day = "0" + day;

            return [year, month, day].join("-");
        };

        const form = reactive({
            id: null,
            type: null,
            lotNumber: null,
            startDate: null,
            endDate: null,
        });

        const mediaInventoryList = ref([]);

        const fetchMediaInventoryList = async () => {
            try {
                const response = await $axios.get("media_inventory/list");
                console.log(response.data);
                mediaInventoryList.value = response.data.mediaInventoryList;
            } catch (error) {
                console.error('Error fetching media inventory list:', error);
            }
        };

        const save = async () => {
            try {
                form.startDate = formatDate(form.startDate);
                form.endDate = formatDate(form.endDate);

                if (formIsEdit.value) {
                    await edit();
                } else {
                    await create();
                }
                addMediaInventoryDialog.value = false;
                fetchMediaInventoryList();
            } catch (error) {
                console.error('Error saving media inventory:', error);
            }
        };

        const create = async () => {
            const response = await $axios.post("media_inventory/create", form);
            mediaInventoryList.value.push({
                ...response.data,
                startDate: formatDate(response.data.startDate),
                endDate: formatDate(response.data.endDate),
            });
        };

        const edit = async () => {
            try {
                const response = await $axios.post(`media_inventory/update/${form.id}`, form);
                const index = mediaInventoryList.value.findIndex(item => item.id === form.id);
                if (index !== -1) {
                    mediaInventoryList.value[index] = {
                        ...response.data,
                        startDate: formatDate(response.data.startDate),
                        endDate: formatDate(response.data.endDate),
                    };
                }
            } catch (error) {
                console.error('Error updating media inventory:', error);
            }
        };

        const showAddMediaInventoryDialog = () => {
            formIsEdit.value = false;
            resetForm();
            addMediaInventoryDialog.value = true;
        };

        const showEditMediaInventoryDialog = (data) => {
            formIsEdit.value = true;
            Object.assign(form, {
                ...data,
                startDate: formatDate(data.startDate),
                endDate: formatDate(data.endDate),
            });
            addMediaInventoryDialog.value = true;
        };

        const resetForm = () => {
            form.id = null;
            form.type = null;
            form.lotNumber = null;
            form.startDate = null;
            form.endDate = null;
        };

        const confirmDelete = () => {
            deleteMediaInventoryDialog.value = true;
        };

        const deleteMedia = async () => {
            try {
                if (formIsEdit.value && form.id) {
                    await $axios.post(`media_inventory/delete/${form.id}`);
                    mediaInventoryList.value = mediaInventoryList.value.filter(
                        item => item.id !== form.id
                    );
                    addMediaInventoryDialog.value = false;
                    deleteMediaInventoryDialog.value = false;
                }
            } catch (error) {
                console.error('Error deleting media inventory:', error);
            }
        };

        const cancelDeleteMediaInventory = () => {
            deleteMediaInventoryDialog.value = false;
        };

        onMounted(() => {
            fetchMediaInventoryList();
        });

        return {
            form,
            formIsEdit,
            addMediaInventoryDialog,
            deleteMediaInventoryDialog,
            mediaInventoryList,
            showAddMediaInventoryDialog,
            showEditMediaInventoryDialog,
            save,
            confirmDelete,
            deleteMedia,
            cancelDeleteMediaInventory
        };
    },
};
</script>

<style scoped>
.table-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
</style>
